@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
	margin: 0;
	font-family: "Open Sans", sans-serif;
	background: #ada996; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#ada996,
		#f2f2f2,
		#dbdbdb,
		#eaeaea,
		#ada996
	); /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, right top,
		from(#ada996),
		color-stop(#f2f2f2),
		color-stop(#dbdbdb),
		color-stop(#eaeaea),
		to(#ada996)
	);
	background: -webkit-linear-gradient(
		left,
		#ada996,
		#f2f2f2,
		#dbdbdb,
		#eaeaea,
		#ada996
	);
	background: linear-gradient(
		to right,
		#ada996,
		#f2f2f2,
		#dbdbdb,
		#eaeaea,
		#ada996
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.app {
	text-align: center;
	font-family: "Montserrat", sans-serif;
	padding: 1rem;
}

.fade {
	-webkit-animation: fadeIn 1s forwards;
	        animation: fadeIn 1s forwards;
}

@-webkit-keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@-webkit-keyframes fadeInHide {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes fadeInHide {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.hide {
	-webkit-animation: fadeInHide 1s forwards;
	        animation: fadeInHide 1s forwards;
}

.search-field {
	width: 25%;
	padding: 0.75rem 1rem;
	border-radius: 1rem;
	outline: none;
	font-size: 1rem;
	border: solid #333;
	text-align: center;
}
.loading {
	margin: auto;
	border-radius: 50%;
	width: 5rem;
	height: 5rem;
	border-left: 1rem solid #00d2ff;
	border-right: 1rem solid #00d2ff;
	border-top: 1rem solid #333;
	border-bottom: 1rem solid #333;
	-webkit-animation: loadingAnimation 2.5s linear infinite;
	        animation: loadingAnimation 2.5s linear infinite;
}

@-webkit-keyframes loadingAnimation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	25% {
		border-top: 1rem solid #00d2ff;
		border-bottom: 1rem solid #00d2ff;
		border-right: 1rem solid #333;
		border-left: 1rem solid #333;
	}
	75% {
		border-right: 1rem solid #00d2ff;
		border-left: 1rem solid #00d2ff;
		border-top: 1rem solid #333;
		border-bottom: 1rem solid #333;
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes loadingAnimation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	25% {
		border-top: 1rem solid #00d2ff;
		border-bottom: 1rem solid #00d2ff;
		border-right: 1rem solid #333;
		border-left: 1rem solid #333;
	}
	75% {
		border-right: 1rem solid #00d2ff;
		border-left: 1rem solid #00d2ff;
		border-top: 1rem solid #333;
		border-bottom: 1rem solid #333;
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.card-group {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.card {
	width: 15rem;
	border-radius: 1rem;
	border: 0.5rem solid rgb(3, 150, 183);
	margin: 1rem;
	padding: 0.75rem;
	background: #00d2ff; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to bottom,
		#00d2ff,
		#3a7bd5
	); /* Chrome 10-25, Safari 5.1-6 */
	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(#00d2ff),
		to(#3a7bd5)
	);
	background: -webkit-linear-gradient(
		top,
		#00d2ff,
		#3a7bd5
	);
	background: linear-gradient(
		to bottom,
		#00d2ff,
		#3a7bd5
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	box-shadow: 0.5rem 0.5rem 0.75rem rgba(0, 0, 0, 0.25);
	-webkit-transition: -webkit-transform 0.5s;
	transition: -webkit-transform 0.5s;
	transition: transform 0.5s;
	transition: transform 0.5s, -webkit-transform 0.5s;
}
.card:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.card * {
	overflow: hidden;
	text-overflow: ellipsis;
}
.card img {
	border: 0.15rem solid black;
}
.card .description {
	border: 0.2rem solid rgba(0, 0, 0, 0.05);
}
.card .name {
	margin-bottom: 0.25rem;
	font-size: 1.5rem;
	text-transform: capitalize;
}
.card .email {
	font-size: 0.75rem;
}

